.menu {
  @extend %cover;
  @extend %column;
  height: 100vh;
  background-color: black;
  z-index: 2;
  transform: translateX(-101%);
  z-index: z('preloader');
}

.menu:before {
content: ' ';
display: block;
position: absolute;
left: 0;
top: 0;
width: 100%;
height: 100%;
//z-index: 1;
z-index: z('preloader');
opacity: 0.15;
background-image: url('images/logo-studio-ln10-white.svg');
background-size: contain;
background-position: 50% 50%;
background-repeat: no-repeat;
}

.menu__item__container {
width: 100%;
height: 100%;
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
gap: 60px;
}

.menu__item__container a {
color: white;
text-transform: uppercase;
text-decoration: none;
position: relative;
font-family: $font-avantgarde-st;
font-size: calc(25px + (80 - 25) * ((100vw - 360px) / (1600 - 360)));
//z-index: 2;
z-index: z('preloader');
}

.menu__item__text {
pointer-events: none;
display: block;
line-height: 1;
position: relative;
}

.menu__item > div:nth-child(1) span {
will-change: transform;
transform-style: preserve-3d;
transition: 0.5s;
transition-delay: calc(0.05s * var(--index));
transform-origin: bottom;
display: inline-block;
}

.menu__item > div:nth-child(2) span {
will-change: transform;
transform-style: preserve-3d;
transition: 0.5s;
transition-delay: calc(0.05s * var(--index));
transform-origin: top;
display: inline-block;
transform: translate3d(0, 100%, 0) rotateX(-90deg);
}

.menu__item:hover > div:nth-child(1) span {
transform: translate3d(0, -100%, 0) rotateX(-90deg);
color: rgb(86, 86, 86);
}

.menu__item:hover > div:nth-child(2) span {
transform: translate3d(0, 0, 0) rotateX(0deg);
color: rgb(86, 86, 86);
}



